<template>
    <div class="others pdlr16">
        <div class="others_list">
            <ul class="flexbox list3">
                <li class="" v-for="item in otherList"><router-link :to="{path:item.url,query:item.params}">{{item.title}}</router-link></li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    name:'others',
    data(){
        return{
            otherList:[
                {title:'画布',url:'/drawingboard',params:{}},
                {title:'学习',url:'/studylist',params:{studytype:'studys'}},
                {title:'免费API',url:'/studylist',params:{studytype:'study_api'}},
            ]
        }
    },
    mounted(){

    },
    methods:{}
}
</script>
<style>
    .others_list{
        padding: 20px 0;
    }
    .others_list li{
        text-align: center;
        box-shadow: 0px 0px 10px 0px rgb(230, 230, 230);
        border-radius: 4px;
        margin-bottom: 16px;
    }
    .others_list li a{
        padding: 10px;
    }
</style>



